// Background image asset management
//$env: "dev";
$env: 'prod';
@mixin background-image($img) {
  @if $env == "dev" {
    background-image: url("/assets/images/#{$img}");
  }
  @if $env == "prod" {
    background-image: url("/wp-content/themes/nabshow-lv-child-2021/assets/images/#{$img}");
  }
}
