// Font imports and variable

// Almost entire site uses brandon-grotesque
$font__primary: brandon-grotesque, sans-serif;
// Top nav uses proxima-nova
$font__secondary: proxima-nova, sans-serif;

$weight__regular: 400;
$weight__medium: 500;
$weight__semibold: 600;
$weight__bold: 700;
$weight__black: 900;

