/* 
  HTML, BODY, global
  ======================================= */

html,
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: $font__primary;
  color: $color__white;
}

/* 
  Headings 
  ======================================= */
.nabshow_2021 {
  h1, h2, h3, h4, h5, h6, p, blockquote, li {
    font-family: $font__primary;
    color: $color__white;
  }
}
.h {
}

.h-xxl {
  @include bp-768 {
    @include primary-black-70-70;
  }
}
.h-xl {
  @include primary-black-20-30;
  margin-bottom: rem(20);
  @include bp-768 {
    @include primary-black-50-60;
  }
}
.h-lg {
  @include primary-black-20-30;
  margin-bottom: rem(20);
  @include bp-768 {
    @include primary-black-45-55;
  }
}
.h-md {
  @include primary-black-22-32;
  margin-bottom: rem(20);
  @include bp-768 {
    @include primary-black-54-57;
    margin-bottom: rem(20);
  }
}
.h-sm {
  @include bp-768 {
    @include primary-bold-20-20;
  }
}
.h-xs {
  @include bp-768 {
    @include primary-black-16-16-20;
  }
}

/* 
  Text
  =======================================
  These classes wrap the target paragraphs
  ======================================= */

.intro-body-text {
  max-width: rem(734);
  margin-bottom: rem(18);
  @include bp-768 {
    margin-bottom: rem(60);
  }
  p {
    @include primary-medium-italic-16-24;
    margin-bottom: rem(30);
    @include bp-768 {
      @include primary-medium-italic-16-24;
      margin-bottom: rem(30);
    }
  }
}

.body-text-small {
  p {
    @include bp-768 {
      @include primary-regular-17-24;
    }
  }
}

.body-text {
  p {
    @include bp-768 {
      @include primary-regular-18-30;
    }
  }
}

.body-text-medium {
  p {
    @include primary-regular-17-31;
    @include bp-768 {
      @include primary-medium-24-46;
    }
  }
}

%body-text-small {
  @include bp-768 {
    @include primary-regular-17-24;
  }
}

%body-text {
  @include bp-768 {
    @include primary-regular-18-30;
  }
}

%body-text-medium {
  @include primary-regular-17-31;
  @include bp-768 {
    @include primary-medium-24-46;
  }
}

/* 
  Quotes
  =======================================
  Blockquotes etc
  ======================================= */

.pullquote {
  position: relative;
  padding: rem(40) 0;
  text-align: center;
  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background: linear-gradient(
      285.1deg,
      rgba(158, 69, 147, 0) -15.62%,
      $color__purple-light 146.86%
    );
    width: rem(125);
    height: rem(5);
  }
  &::before {
    top: 0;
  }
  &::after {
    bottom: 0;
  }
  @include bp-768 {
    text-align: left;
    &::before,
    &::after {
      left: 0;
      transform: translateX(0);
    }
  }
  p {
    @include primary-black-25-40;
    text-transform: uppercase;
    margin-bottom: rem(20);
    @include bp-768 {
      @include primary-black-31-44;
    }
  }
  cite {
    @include primary-black-12-12-20;
    text-transform: uppercase;
    font-style: normal;
  }
}
